body {
  background-color: #b8f9f9;
}

.auth-form {
  position: fixed;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  width: 340px;
  min-width: 340px;
  height: 450px;
  padding: 30px;
  padding-top: 50px;
  background: #FFFFFF;
  box-shadow: 0px 0px 12px rgba(0, 0, 0, 0.14);
  border-radius: 12px;
}

.auth-form:focus, .auth-form:hover, .auth-form:active {
  box-shadow: 0px 0px 25px rgba(0, 0, 0, 0.14);
}

.auth-form__title {
  font-family: 'Inter';
  font-style: normal;
  font-weight: 500;
  font-size: 20px;
  line-height: 20px;
  text-align: center;
  color: #2400FF;
  margin-bottom: 40px;
}

.button-wrapper {
  display: flex;
  flex-direction: column;
}

.main-btn_auth {
  margin-bottom: 15px;
}

@media screen and (max-width: 992px) {
  .auth-form {
    position: static;
    margin: auto;
    margin-top: 10%;
    transform: none;
    max-width: 340px;
  }
}

@media screen and (max-width: 576px) {
  .auth-form {
    width: 90%;
    max-width: auto;
    min-width: auto;
    margin: none;
  }
}

.registry-form {
  position: static;
  transform: none;
  margin: auto;
  margin-top: 25px;
  height: max-content;
}

.registry-form__title {
  margin-bottom: 15px;
}

.error {
  display: flex;
  flex-direction: column;
  align-items: center;
  position: fixed;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
}

.error__number {
  margin-top: 0;
  margin-bottom: 20px;
  font-family: 'Inter';
  font-style: normal;
  font-weight: 500;
  color: #2400FF;
  font-size: 40px;
  line-height: 40px;
}

.error__info {
  margin-bottom: 70px;
  font-family: 'Inter';
  font-style: normal;
  font-weight: 500;
  color: #2400FF;
  font-size: 20px;
  line-height: 20px;
}

body {
  background-color: #FFFFFF;
}

.container {
  position: relative;
  max-width: 1110px;
  margin: auto;
}

.account {
  display: flex;
  flex-direction: column;
  max-width: 510px;
  margin: auto;
  padding-top: 35px;
}

.account__avatar {
  position: relative;
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-bottom: 60px;
}

.account__avatar__img {
  width: 150px;
  height: 145px;
  margin-bottom: 30px;
}

.account__avatar__name {
  font-family: 'Inter';
  font-style: normal;
  font-weight: 500;
  font-weight: 600;
  font-size: 16px;
  line-height: 16px;
  color: #2400FF;
}

.account__avatar__change {
  position: absolute;
  left: 50%;
  top: 60%;
  transform: translate(-50%, -50%);
  width: 65px;
  text-align: center;
  font-family: 'Inter';
  font-style: normal;
  font-weight: 500;
  font-size: 13px;
  line-height: 14px;
  color: #FDE402;
  opacity: 0;
}

.account__avatar__change:hover {
  opacity: 1;
}

.account__input {
  margin-bottom: 60px;
}

.account__check, .account__back {
  align-self: start;
  margin-bottom: 20px;
  font-size: 13px !important;
  line-height: 13px !important;
}

.account__back {
  color: #FF2F2F !important;
}

.account__back:focus, .account__back:hover, .account__back:active {
  color: #780202 !important;
}

.back {
  position: absolute;
  left: 0;
  top: 0;
  bottom: 0;
  display: flex;
  align-items: center;
  padding: 0 18px;
  background-color: #FBFBFB;
}

.back__arrow {
  width: 30px;
  height: 30px;
  border-radius: 50%;
  background: url("../static/image/back-arrow.svg") no-repeat;
  background-color: #FDE402;
  background-position: 50%;
}

.back__arrow:focus, .back__arrow:hover, .back__arrow:active {
  background-color: #6E6402;
}

@media screen and (max-width: 768px) {
  .account {
    max-width: 80%;
    margin-right: 5%;
  }
  .back {
    left: 0;
    padding-left: 0;
    padding-right: 8px;
  }
}

.account__button {
  align-self: center;
}

.account__avatar__download {
  position: fixed;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  z-index: 15;
  background-color: rgba(141, 141, 141, 0.4);
}

.account__avatar__form {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  position: fixed;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  width: 340px;
  height: 260px;
  padding: 30px 40px;
  background-color: #FFFFFF;
  border-radius: 12px;
}

.account__avatar__title {
  align-self: center;
  font-family: 'Inter';
  font-style: normal;
  font-weight: 500;
  font-size: 15px;
  line-height: 20px;
  color: #2400FF;
}

.account__avatar__label {
  align-self: center;
  text-align: center;
  width: 120px;
  font-family: 'Inter';
  font-style: normal;
  font-weight: 500;
  font-size: 12px;
  line-height: 16px;
  color: #02EEFD;
  text-decoration: underline;
}

.account__avatar__input {
  opacity: 0;
  visibility: hidden;
  position: absolute;
}

.change__field__input {
  text-align: right;
  font-family: 'Inter';
  font-style: normal;
  font-weight: 500;
  font-size: 13px;
  line-height: 13px;
  color: #6E6402;
  border: none;
  outline: none;
}

.main-btn {
  width: 100%;
  height: 40px;
  max-width: 280px;
  font-family: 'Inter';
  font-style: normal;
  font-weight: 500;
  font-size: 13px;
  line-height: 13px;
  background-color: #FDE402;
  color: #FFFFFF;
  border-radius: 8px;
  border: none;
  cursor: pointer;
}

.main-btn:focus, .main-btn:hover, .main-btn:active {
  background-color: #6E6402;
}

.auth-form__input {
  width: 100%;
  max-width: 280px;
  border: none;
  border-bottom: 1px solid #FDE402;
  margin-bottom: 30px;
  font-family: 'Inter';
  font-style: normal;
  font-weight: 500;
  font-size: 13px;
  line-height: 13px;
  color: #2400FF;
}

.auth-form__input:focus, .auth-form__input:hover, .auth-form__input:active {
  outline: none;
  box-shadow: none;
}

.auth-form__input_label {
  font-size: 13px;
  line-height: 13px;
  color: #02EEFD;
}

.auth-form__link {
  display: inline-block;
  text-align: center;
  font-family: 'Inter';
  font-style: normal;
  font-weight: 500;
  font-size: 11px;
  line-height: 11px;
  color: #02EEFD;
  text-decoration: none;
}

.auth-form__link:focus, .auth-form__link:hover, .auth-form__link:active {
  color: #00818a;
  outline: none;
}

.account__field {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 10px 0;
  border-bottom: 1px solid #EFEFEF;
  font-family: 'Inter';
  font-style: normal;
  font-weight: 500;
  font-size: 13px;
  line-height: 13px;
}

.account__field__data {
  color: #2400FF;
}

.account__field__value {
  color: #6E6402;
}

.change__field {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 10px 0;
  border-bottom: 1px solid #EFEFEF;
  font-family: 'Inter';
  font-style: normal;
  font-weight: 500;
  font-size: 13px;
  line-height: 13px;
}

.change__field__data {
  color: #2400FF;
}

.change__field__value {
  color: #6E6402;
}
